import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VuePrism from 'vue-prism'

import 'prismjs/themes/prism.css'
import '@/assets/scss/index.scss'

Vue.use(VuePrism)
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
